.main-nav {
  width: 100%;
}

@media (max-width: $tablet-width-only) {
  .page-header--main-nav-open .main-nav {
    display: block;
    position: absolute;
    background-color: $middle-gray;
  }

  .page-header--main-nav-closed .main-nav {
    display: none;
  }
}

.page-header--nojs .main-nav {
  display: block;
}

.main-nav__list {
  line-height: 32px;
  font-weight: 700;
  list-style: none;
  padding: 0;
  margin: 0;
}

.main-nav__item {
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  padding-top: 22px;
  padding-bottom: 24px;
  box-shadow: 0 1px 0 $black, inset 0 1px 0 $opacity-015-white;
}

.main-nav__link {
  color: $white;
  text-decoration: none;

  &:hover {
    color: $pink;
  }

  &:active {
    color: $opacity-03-white;
  }

  &--active {
    border-bottom: 2px solid $white;
    padding-bottom: 6px;
  }
}

@media (min-width: $desktop-width) {
  .main-nav {
    width: auto;
    margin-top: 13px;
  }

  .main-nav__list {
    display: flex;
    justify-content: space-between;

    line-height: 18px;
    font-weight: 400;
  }

  .main-nav__item {
    padding: 0;
    box-shadow: none;
    text-transform: initial;
  }

  .main-nav__item + .main-nav__item {
    margin-left: 79px;
  }
}
